import React, { Component } from 'react';
import FeatureRace from "components/FeatureRace";
import EventCategory from "components/EventCategory";
import EventSection from "components/EventSection";
import { BoxEventContainer } from "./styled";

import _ from 'lodash';

class Event extends Component {
    componentDidMount() {
        this.props.getFeatureRace();
    }

    render() {
        const {featureRace} = this.props;
        const data = _.get(featureRace.data, 'data', []);

        return (
            <>
                <section>
                    <FeatureRace data={ data.featured }/>
                </section>
                <BoxEventContainer>
                    <section>
                        <EventCategory/>
                    </section>
                    { Object.keys(data).filter(name => !['updatedAt', 'featured', '_id'].includes(name)).map((key) => {
                        const sectionTitle = key.replace(/([A-Z]+)*([A-Z][a-z])/g, "$1 $2");
                        const eventData = data[key];

                        return (
                            <section key={ key }>
                                <EventSection data={ eventData } title={ sectionTitle }/>
                            </section>
                        )
                    }) }
                </BoxEventContainer>
            </>
        );
    }
}

export default Event;