import React from 'react';
import {BoxRaceContainer} from "styled";
import EventItem from 'components/EventItem';
import _ from 'lodash';

class Cycling extends React.Component {
    componentDidMount() {
        this.props.getCyclingRace();
    }

    render() {
        const {cyclingRace} = this.props;
        const data = _.get(cyclingRace,'data.data',[]);

        return (
            <BoxRaceContainer>
                <h2 className="race-heading">{data.length} cycling events</h2>
                {!_.isEmpty(data) && data.map((item,key)=>{
                    return (
                        <EventItem data={item} key={key} card={false} />
                    )
                })}
            </BoxRaceContainer>
        );
    }
}

export default Cycling;
